@use "../mixins";

.social {

}

.social__list {
  @include mixins.reset-list;
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  max-width: 156px;
}

.social__item {
  min-width: 48px;
  min-height: 48px;
}

.social__link {
  display: block;
  padding: 12px;
  background-color: #81B3D2;

  &:hover {
    background-color: #669EC0;
  }

  &:active {
    background-color: #5496BD;
  }

  &:active svg {
    opacity: 0.3;
  }
}

.social__icon {
  width: 24px;
  height: 24px;
}
